<template>
  <div class="reply-bar">
    <!-- 未登录状态 -->
    <router-link class="no-login" v-if="!userInfo.userId" to="/login">
    </router-link>
    <div class="reply-main">
      <textarea rows="8" class="text reply-content" placeholder="写评论" :class="{'err':hasErr}" v-model="content" @blur="isBlur" @focus="isFocus">
      </textarea>
      <div class="reply-button">
        <div class="icons">
          <!-- 评论按钮 -->
          <span class="icon-message">
            <span class="reply-num" v-text="topic.replies.length"></span>
          </span>
          <!-- 收藏 -->
          <!-- <span></span> -->
          <span></span>
          <!-- <span class="icon-star"></span> -->
          <span class="icon-share"></span>
        </div>
        <button class="btn-ok" @click="addReply" :class="{'show':focused}">确定</button>
      </div>
    </div>
  </div>
</template>
<style lang="scss">
@import '~style/base/_var';

.reply-list .reply-bar {
  z-index: 1099;
}

.reply-bar {
  position: fixed;
  z-index: 1024;
  bottom: 0;
  left: 0;
  padding: 6px 0;
  width: 100%;
  background-color: #eee;
  overflow: hidden;
  .no-login {
    position: absolute;
    z-index: 9;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
  .reply-main {
    position: relative;
    height: 100%;
    display: flex;
    align-items: flex-end;
  }
  // 评论框
  .reply-content {
    position: relative;
    margin-left: $gap;
    margin-right: $gap;
    border: none;
    background-color: #fff;
    border-radius: 4px;
    height: 28px;
    line-height: 28px;
    transition: height .3s ease, padding .3s ease;
    padding: 0 $gap;
    flex: 1;
    resize: none;
    &:focus {
      line-height: 1.3;
      height: 6em;
      padding-top: 5px;
    }

    &.err {
      box-shadow: 0 0 4px #e61b1b inset;
    }
  }
  .btn-ok {
    position: absolute;
    z-index: 1600;
    top: -99px;
    width: 95%;
    height: 32px;
    line-height: 32px;
    color: #fff;
    font-size: 14px;
    background: none;
    border: none;
    text-align: center;
    color: $title-color;
    vertical-align: middle;
    transition: .3s ease;
    &.show {
      top: -45px;
    }
  }
  .reply-button {
    position: relative;
    .icons {
      display: flex;
      position: relative;
      z-index: 1;
    }
    span {
      display: block;
      height: 100%;
      position: relative;
      font-size: 24px;
      color: $tips-color;
      min-width: 50px;
      text-align: center;
      flex: 1;
    }
    .reply-num {
      position: absolute;
      top: -4px;
      left: 30px;
      border-radius: 99px;
      font-size: 10px;
      background-color: red;
      color: #fff;
      text-align: center;
      padding: 0 3px;
      min-width: 18px;
      height: 18px;
      line-height: 18px;
    }
  }
}
</style>
<script>
import $ from 'webpack-zepto'
const utils = require('../libs/utils')
const markdown = require('markdown').markdown
import {
  mapGetters
} from 'vuex'

export default {
  replace: true,
  props: ['topic', 'replyId', 'topicId', 'replyTo', 'show'],
  data() {
    return {
      hasErr: false,
      content: '',
      author_txt: '',
      fuckingTimer: null,
      focused: false
    }
  },
  computed: {
    ...mapGetters({
      userInfo: 'getUserInfo'
    })
  },
  mounted() {
    if (this.replyTo) {
      this.content = `@${this.replyTo} `
    }
  },
  methods: {
    isFocus() {
      this.focused = true
      clearTimeout(this.fuckingTimer)
    },
    isBlur() {
      clearTimeout(this.fuckingTimer)
      this.fuckingTimer = setTimeout(() => {
        this.focused = false
      }, 1000)
    },
    addReply() {
      if (!this.content) {
        this.hasErr = true
        this.$alert('请输入评论内容')
      } else {
        let time = new Date()
        let linkUsers = utils.linkUsers(this.content)
        let htmlText = markdown.toHTML(linkUsers) + this.author_txt
        let replyContent = $('<div class="markdown-text"></div>').append(htmlText)[0].outerHTML
        let postData = {
          accesstoken: this.userInfo.token,
          content: this.content + this.author_txt
        }

        if (this.replyId) {
          postData.reply_id = this.replyId
        }
        $.ajax({
          type: 'POST',
          url: `https://cnodejs.org/api/v1/topic/${this.topicId}/replies`,
          data: postData,
          dataType: 'json',
          success: (res) => {
            if (res.success) {
              this.topic.replies.push({
                id: res.reply_id,
                author: {
                  loginname: this.userInfo.loginname,
                  avatar_url: this.userInfo.avatar_url
                },
                content: replyContent,
                ups: [],
                create_at: time
              })

              this.hasErr = false
              this.$alert('评论成功')
              // this.replyCount += 1
              // 更新父组件评论
              this.$emit('reply-count-change')
              window.scrollTo(0, document.body.scrollHeight)
            }
            this.content = ''
            if (this.show) {
              this.show = ''
            }
          },
          error: (res) => {
            var error = JSON.parse(res.responseText)
            this.$alert(error.error_msg)
            return false
          }
        })
      }
    }
  }
}
</script>



