<template>
  <div class="header-box">
    <div class="page-cover" v-if="show&&fixHead" @click="showMenus">
    </div>
    <header id="v-header" :class="{'show':show&&fixHead,'fix-header':fixHead,'no-fix':!fixHead}">
      <div class="toolbar">
        <div class="left">
          <div class="btn-back icon-back" @click="goBack"></div>
          <slot name="left"></slot>
        </div>
        <h1 class="title" v-text="pageType"></h1>
        <div class="right">
          <router-link class="icon-post btn-add" to="/add" v-if="needAdd" v-show="!messageCount || messageCount <= 0">
          </router-link>
          <div v-else class="btn-menu icon-dot" @click="openMenu" v-if="fixHead">
          </div>
          <slot name="right"></slot>
        </div>
      </div>
    </header>
    <nv-menu :show-menu="show" :page-type="pageType" :nick-name="nickname" :profile-url="profileimgurl" v-if="fixHead"></nv-menu>
  </div>
</template>
<script>
export default {
  replace: true,
  props: {
    pageType: String,
    fixHead: {
      type: Boolean,
      default: true
    },
    messageCount: Number,
    needAdd: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      nickname: '',
      profileimgurl: '',
      show: false
    }
  },
  methods: {
    // 返回上一页，相当于history.go(-1)
    goBack() {
      this.$router.go(-1)
    },
    openMenu() {
      $('html, body, #page').addClass('scroll-hide')
      this.show = !this.show
    },
    showMenus() {
      this.show = !this.show
      $('html, body, #page').removeClass('scroll-hide')
    }
  },
  components: {
    'nvMenu': require('./menu.vue')
  }
}
</script>
<style lang="scss">
@import '~style/base/_var';

  #v-header {
    /* border-bottom: 1px solid #e8e8e8; */
    box-shadow: 0 1px 0px $line-color, 0 1px 3px $line-color;

    &.fix-header {
      width: 100%;
      background-color: rgba(255, 255, 255, 1);
      position: fixed;
      top: 0;
      left: 0;
      transition: transform .3s ease;
      z-index: 520;
    }

    &.no-fix {
      width: 100%;
      background-color: #fff;
      overflow: hidden;
    }

    &.show {
      transform: translateX(-200px);
    }
  }

  .toolbar {
    width: 100%;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .btn-menu {
      color: $title-color;
    }

    .title {
      flex: 1;
      text-align: center;
      height: 100%;
      line-height: 44px;
      font-size: 16px;
      position: relative;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      font-weight: normal;
      color: $title-color;
    }

    [class^="btn-"],
    [class*=" btn-"] {
      width: 50px;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 24px;
      text-align: center;
    }

    .num {
      background-color: #80bd01;
      color: #fff;
      width: 20px;
      height: 20px;
      line-height: 20px;
      vertical-align: middle;
      text-align: center;
      border-radius: 50%;
      position: absolute;
      right: 10px;
      top: 10px;
      z-index: 10;
    }

    .icon-dot {
      color: $text-color;
    }

    .icon-post {
      color: $base-color;
    }

    .icon-back {
      width: 60px;
      padding-left: 6px;
      justify-content: flex-start;
      color: darken($base-color, 5%);
    }
  }

  .scroll-hide {
    height: 100%;
    overflow: hidden;
  }
</style>



