<template>
  <div class="user-info">
    <!-- 未登录 -->
    <ul class="login-no" v-if="!userInfo.loginname">
      <li class="login" @click="goEnter">登录</li>
    </ul>
    <!-- 已登录 -->
    <div class="login-yes" v-if="userInfo.loginname" @click="goUser">
      <div class="avertar"><img v-if="userInfo.avatar_url" :src="userInfo.avatar_url"></div>
      <div class="info">
        <p v-if="userInfo.loginname" v-text="userInfo.loginname"></p>
      </div>
    </div>
  </div>
</template>
<script>
import {
  mapGetters
} from 'vuex'
// import userStore from '../vuex/user';
export default {
  replace: true,
  computed: {
    ...mapGetters({
      userInfo: 'getUserInfo'
    })
  },
  methods: {
    goEnter() {
      this.$router.push({
        name: 'login',
        query: {
          redirect: encodeURIComponent(this.$route.path)
        }
      })
    },
    goUser() {
      this.$router.push({
        name: 'user',
        params: {
          loginname: this.userInfo.loginname
        }
      })
    }
  }
}
</script>
<style lang="scss">
@import '~style/base/_var';

/* 侧边栏用户信息区域 */
.user-info {
  padding: 15px;
  font-size: 15px;
  color: #313131;
}

/*未登录*/
.login-no {
  overflow: hidden;

  & > li {
    padding-top: $gap * 2;
    padding-bottom: $gap * 2;
    padding-left: $gap * 5;
    position: relative;
  }

  .login {
      background: url("~img/components/login_icon.png") no-repeat left center;
      background-size: 24px 24px;
  }
}

/*已登录*/
.login-yes {
  height: 100%;
  background: url("~img/components/go_next_icon.png") no-repeat right center;
  background-size: 6px 10px;
  overflow: hidden;
  position: relative;

  .avertar {
    width: 40px;
    height: 40px;
    background: url("~img/components/user.png") no-repeat center center;
    background-size: 40px 40px;
    border-radius: 20px;
    overflow: hidden;
    float: left;

    & > img {
      width: 40px;
      height: 40px;
      display: block;
    }
  }

  .info {
    margin-left: 10px;
    overflow: hidden;
    float: left;
  }

  p {
    width: 85px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: 12px;
    line-height: 12px;
    line-height: 40px;

    &.lh20 {
      line-height: 20px;
    }
  }

  &:after {
    display: block;
    background: url("~img/components/go_icon.png") no-repeat center right;
    background-size: 7px 7px;
  }
}
</style>



