<template>
  <div>
    <nv-head page-type="" :show-menu="showMenu" :need-add="false" :fix-head="true">
    </nv-head>
    <div id="page" :class="{'show-menu':showMenu}" v-if="topic.title">
      <h2 class="topic-title" v-text="topic.title"></h2>
      <section class="author-info">
        <!-- <img class="avatar" :src="topic.author.avatar_url"> -->
        <router-link class="info" :to="{name:'user',params:{loginname:topic.author.loginname}}">
          <img class="avatar" :src="topic.author.avatar_url">
          <div class="text">
            <h3 class="user-name">{{topic.author.loginname}}</h3>
            <time>{{topic.create_at | getLastTimeStr(false)}}</time>
          </div>
        </router-link>
        <div class="bar">
          <button class="btn-follow" @click="toggleCollect">
            <span v-if="data.is_collect" class="icon icon-star"></span>
            <span v-if="!data.is_collect" class="icon icon-star-active"></span>
          </button>
        </div>
      </section>
      <section class='markdown-body topic-content' v-html="topic.content">
      </section>

      <!-- <section>
        <h3 class="recommend">推荐信息</h3>
        <ul class="recommend-list">
          <li><span class="icon-plus2"></span>
            <p>到此一游<br>留下足迹</p>
          </li>
        </ul>
      </section>
       -->
      <!-- 评论列表 -->
      <section class="reply">
        <h3 id="topic-reply" class="topic-reply">
          评论<strong>（{{replyCount}}）</strong>
        </h3>
        <ul class="reply-list">
          <li v-for="item in topic.replies">
            <section class="user">
              <router-link :to="{name:'user',params:{loginname:item.author.loginname}}">
                <img class="head" :src="item.author.avatar_url">
              </router-link>
            </section>
            <section class="content">
              <div class="info">
                <div class="user-name" v-text="item.author.loginname"></div>
                <div class="like" @click="upReply(item)">
                  {{item.ups.length}}
                  <span class="icon icon-good" :class="{'uped':isUps(item.ups)}"></span>
                </div>
              </div>
              <div class="reply-main">
                <div class="replay-content" v-html="item.content"></div>
              </div>
              <div class="reply-footer" @click="addReply(item.id)">
                <time class="time">发布于：{{item.create_at | getLastTimeStr(false)}}</time>
                <span class="btn-reply">
                  <span class="icon icon-reply"></span>
                </span>
              </div>

              <!-- @评论框 -->
              <nv-reply-bar
                :topic.sync="topic"
                :topic-id="topicId"
                v-on:reply-count-change="replyCountTotal"
                :reply-count="topic.replies.length"
                :reply-id="item.id"
                :reply-to="item.author.loginname"
                :show.sync="curReplyId" v-if="userInfo.userId && curReplyId === item.id">
                </nv-reply-bar>
            </section>
          </li>
        </ul>
      </section>

      <!-- 默认评论框 -->
      <nv-reply-bar
        :topic="topic"
        :topic-id="topicId"
        v-on:reply-count-change="replyCountTotal"
        :reply-count="topic.reply_count">
        </nv-reply-bar>

    </div>
    <div class="no-data" v-if="noData">
      <div class="icon-empty"></div>
      <p>该话题不存在!</p>
    </div>
  </div>
</template>

<style lang="scss" src="style/topic.scss">
</style>

<script>
import $ from 'webpack-zepto'
import utils from '../libs/utils.js'
import nvHead from '../components/v-header.vue'
import nvReplyBar from '../components/reply-bar.vue'
import { mapGetters } from 'vuex'

export default {
  data() {
    return {
      showMenu: false, // 是否展开左侧菜单
      topic: {}, // 主题
      noData: false,
      topicId: '',
      data: {},
      collectLock: false, // 是否收藏
      replyCount: '',
      curReplyId: ''
    }
  },
  ready() {
    this.toggleCollect()
  },
  attached() {
  },
  computed: {
    ...mapGetters({
      userInfo: 'getUserInfo'
    })
  },
  mounted() {
    // 隐藏左侧展开菜单
    this.showMenu = false

    // 获取url传的tab参数
    this.topicId = this.$route.params.id

    // 加载主题数据
    $.get('https://cnodejs.org/api/v1/topic/' + this.topicId, (d) => {
      if (d && d.data) {
        this.topic = d.data
        this.replyCount = d.data.reply_count
      } else {
        this.noData = true
      }
    })
  },
  methods: {
    // 评论数 + 1
    replyCountTotal() {
      this.replyCount += 1
    },
    getTabInfo(tab, good = false, top, isClass) {
      return utils.getTabInfo(tab, good, top, isClass)
    },
    getLastTimeStr(time, ago) {
      return utils.getLastTimeStr(time, ago)
    },
    // 是否点赞
    isUps(ups) {
      return $.inArray(this.userInfo.userId, ups) >= 0
    },
    // 添加回复
    addReply(id) {
      this.curReplyId = id
      if (!this.userInfo.userId) {
        this.$router.push({
          name: 'login',
          params: {
            redirect: encodeURIComponent(this.$route.path)
          }
        })
      }
    },
    // 评论点赞
    upReply(item) {
      if (!this.userInfo.userId) {
        this.$router.push({
          name: 'login',
          params: {
            redirect: encodeURIComponent(this.$route.path)
          }
        })
      } else {
        $.ajax({
          type: 'POST',
          url: 'https://cnodejs.org/api/v1/reply/' + item.id + '/ups',
          data: {
            accesstoken: this.userInfo.token
          },
          dataType: 'json',
          success: (res) => {
            if (res.success) {
              if (res.action === 'down') {
                let index = $.inArray(this.userInfo.userId, item.ups)
                item.ups.splice(index, 1)
              } else {
                item.ups.push(this.userInfo.userId)
              }
            }
          },
          error: (res) => {
            let error = JSON.parse(res.responseText)
            this.$alert(error.error_msg)
            return false
          }
        })
      }
    },
    toggleCollect() {
      if (this.data.is_collect) {
        this.collect('de_collect')
      } else {
        this.collect('collect')
      }
    },

    collect(tag) {
      let collectEnum = {
        de_collect: false,
        collect: true
      }
      let tagBoolean = collectEnum[tag]

      if (this.collectLock) {
        this.$alert('数据传输中，请等待一会儿')
        return
      }

      this.collectLock = true

      // Vue.set(this.data, 'is_collect', tagBoolean)
      this.$set(this.data, 'is_collect', tagBoolean)

      if (!this.userInfo.userId) {
        this.$router.push({
          name: 'login',
          params: {
            redirect: encodeURIComponent(this.$route.path)
          }
        })
      } else {
        $.ajax({
          type: 'POST',
          url: `https://cnodejs.org/api/v1/topic_collect/${tag}`,
          data: {
            accesstoken: this.userInfo.token,
            topic_id: this.topicId
          },
          dataType: 'json',
          success: (res) => {
            if (!res.success) {
              this.$set(this.data, 'is_collect', tagBoolean)
            }
            this.collectLock = false
          },
          error: (res) => {
            this.collectLock = false
            this.$set(this.data, 'is_collect', tagBoolean)

            let error = JSON.parse(res.responseText)
            this.$alert(error.error_msg)
            return false
          }
        })
      }
    }
  },
  components: {
    nvHead,
    // nvReply,
    nvReplyBar
  }
}
</script>



