<template>
  <section id="sideBar" class="nav-list" :class="{'show':showMenu}">
    <user-info></user-info>
    <section class="list-ul">
      <router-link class="icon-home item" to="/index">首页</router-link>
      <router-link class="icon-near item" to="/near">附近</router-link>
      <router-link class="icon-post item" to="/add">发布</router-link>
      <router-link class="icon-follow item" to="/follow">关注</router-link>
      <router-link class="icon-my item" to="/my">我的</router-link>
    </section>
  </section>
</template>
<script>
export default {
  replace: true,
  props: ['showMenu', 'pageType', 'nickName', 'profileUrl'],
  components: {
    'userInfo': require('./user-info.vue')
  }
}
</script>
<style lang="scss">
@import '~style/base/_var';

/*侧边栏*/
.nav-list {
  position: fixed;
  top: 0;
  bottom: 0;
  right: -200px;
  width: 200px;
  background-color: #fff;
  color: #313131;
  transition: all .3s ease;
  z-index: 3000;
  &.show {
    transform: translateX(-200px);
  }
}


/*侧边栏列表*/

.list-ul {
  margin: 0 24px;
  border-top: 1px solid $line-color;
  overflow: hidden;
  padding-top: 9%;

  .item {
    display: block;
    font-size: 14px;
    font-weight: 200;
    padding: 9% $gap;
    text-align: left;
    text-indent: 1px;
    line-height: 15px;
    color: #313131;
    &:last-child {
      margin-bottom: 50px;
    }
    &:before {
      color: #2c3e50;
      margin-right: $gap * 2;
    }
  }
  .line {
    border-top: 1px solid $line-color;
  }
}
</style>



